<template>
	<view class="box">
		<view class="login-main">
			<view class="login-tips font-bold">
				找回密码
			</view>
			<view class="login-box">
				<view class="login-line">
					<input class="input" type="text" v-model="mobile" maxlength="11" placeholder="请输入手机号码" />
				</view>
				<view class="login-line">
					<input class="input" type="text" v-model="validateCode" maxlength="6" placeholder="请输入手机验证码" />
					<view class="yzm-btn border-btn" @tap="getPhoneCode">
						{{yzm_text}}
					</view>
				</view>
				<view class="login-line">
					<input class="input" type="text" password="password" v-model="passWord" maxlength="20" placeholder="请输入6-20位的新密码" />
				</view>
				<view class="login-line">
					<input class="input" type="text" password="password" v-model="RepassWord" maxlength="20" placeholder="再次输入新密码" />
				</view>
				<view class="bg-btn bg-primary" @tap="changePass">
					修改
				</view>
				
			</view>
		</view>
	</view>
</template>

<script>
	let t=60;
	let cut=null;
	export default {
		data() {
			return {
				mobile: "",
				yzm_text: "获取验证码",
				validateCode:"",
				passWord:"",
				RepassWord:""
			}
		},
		onLoad() {
			
		},
		methods: {
			changePass(){
				let phonereg = /^1[3456789]\d{9}$/
				if (!(phonereg.test(this.mobile))) {
					this.$box.toast('请填写正确的手机号码', 'none');
					return;
				}
				if(!this.validateCode||!this.passWord){
					this.$box.toast('请正确填写所有信息', 'none');
					return;
				}
				if(this.passWord!=this.RepassWord){
					this.$box.toast('两次密码输入不一致', 'none');
					return;
				}
				this.$api.get('/login/resetPassword',{params:{
					mobile:this.mobile,
					verificationCode:this.validateCode,
					passWord:this.passWord
				}}).then(res=>{
					if(res.code==200){
						this.$box.confirm('密码已重置，请重新登录',false);
						this.mobile=""						this.yzm_text= "获取验证码",						this.validateCode="",						this.passWord="",						this.RepassWord="";
						t = 60;
						self.yzm_text = "获取验证码"
						clearInterval(cut)
						cut=null
						
					}
					
				}).catch(err=>{
					console.log(err);
				})
			},
			getPhoneCode() {
				let self = this;
				let phonereg = /^1[3456789]\d{9}$/
				if (!(phonereg.test(this.mobile))) {
					this.$box.toast('请填写正确的手机号码', 'none');
					return;
				}
				if (cut) {
					return
				}
				this.$api.get('/wechat/email/resetPassword', {
					params: {
						mobile: this.mobile
					}
				}).then(res => {
					if (res.code == 200) {
						this.$box.toast(res.msg);
						cut = setInterval(function() {
							self.cutTime()
						}, 1000)
					}
				})
			
			
			},
			cutTime() {
				let self = this;
				if (t > 0) {
					t--;
					self.yzm_text = t + "S后重试"
				} else {
					t = 60;
					self.yzm_text = "获取验证码"
					clearInterval(cut)
					cut=null
				}
			},
		},
		onUnload() {
			t = 60;
			this.yzm_text = "获取验证码"
			clearInterval(cut)
			cut=null
		}
	}
</script>

<style lang="scss">
	.rule {
		display: flex;
		align-items: center;
		font-size: 20rpx;
		.iconfont {
			font-size: 30rpx;
			padding: 8rpx;
		}
		.iconxuanzhong {
			color: $color-primary;
		}
		.url {
			color: $color-primary;
		}
	}
	.back-home2 {
		border:2px solid $color-primary !important;
		background: none !important;
		color: $color-primary!important;;
	}
	.login-main {
		width: 100%;
	}

.bg-primary {
		background: $color-primary;
		margin: 60rpx 0;
		height: 50rpx;
		line-height: 50rpx;
		border-radius: 100rpx;
		font-size: 30rpx;
	}

	.box {
		width: 100%;
		height: calc(100vh - var(--window-top));
		background: white;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.login-tips {
		width: calc(100% - 80rpx);
		margin: 0 auto;
		font-size: 60rpx;
		margin-bottom: 70rpx;
	}

	.login-box {
		width: calc(100% - 80rpx);
		margin: 0 auto;

		.login-line {
			display: flex;
			width: 100%;
			align-items: center;
			height: 100rpx;
			line-height: 100rpx;
			font-size: 30rpx;
			border-bottom: 1px solid #eaeaea;

			.title {
				width: 130rpx;
				flex-shrink: 0;
			}

			.input {
				font-size: 26rpx;
				flex-grow: 1;
			}
		}
	}

	.yzm-btn {
		padding: 12rpx 20rpx;
		margin-left: 10rpx;
		height: 26rpx;
		line-height: 26rpx;
		flex-shrink: 0;
	}

	.other-login {
		width: 100%;
		font-size: 26rpx;
		width: 100%;
		text-align: center;
		color: #a1a1a1;
		margin-top: 60rpx;

		.iconfont {
			font-size: 50rpx;
			margin-top: 40rpx;
		}
	}
</style>
